import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home/home'
import Login from "../views/login/login"
import Users from "../views/home/component/Users"
import Role from "../views/home/component/Role"
import Right from "../views/home/component/Right"
import Banner from "../views/home/component/Banner"
import Report from "../views/home/component/Report"
import Article from "../views/home/component/Article"
import ArticleEdit from "../views/home/component/ArticleEdit"
Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    children:[
        {
            path:'',
            redirect:'report'
        },
        {
          path:'users',
          name:'users',
          component:Users
        },
        {
            path:'role',
            name:'role',
            component:Role
        },
        {
            path:'right',
            name:'right',
            component:Right
        },
        {
            path:'banner',
            name:'banner',
            component:Banner
        },
        {
            path:'report',
            name:'report',
            component:Report
        },
        {
            path:'article',
            name:'article',
            component:Article
        },
        {
            path:'articleedit',
            name:'articleedit',
            component:ArticleEdit
        }
    ]
  },
    {
      path:'/login',
      name:'login',
      component:Login
    }


]


const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next)=>{
    const isLogin=localStorage.myblogToken?true:false;
    if(to.path==="/login"||to.path==="/register"){
        next();
    }else{
        isLogin?next():next("/login")
    }
});

export default router
